import Stack from '@mui/material/Stack';
import * as React from 'react';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';

const Info = ({details}) => {

  return (
    <Stack>
      {details.tel ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
              
            </Typography>
            <Typography>
              {details.tel}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.lctCl ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
               
            </Typography>
            <Typography>
              {details.lctCl}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.induty ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
               
            </Typography>
            <Typography>
              {details.induty}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.operPdCl ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
              
            </Typography>
            <Typography>
              {details.operPdCl}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.operDeCl ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
              
            </Typography>
            <Typography>
              {details.operDeCl}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.homepage ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
              
            </Typography>
            <Typography>
              <a href={details.homepage}> </a>
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.posblFcltyCl ? (
        <Stack>
          <Stack direction="row" >
            <Typography
              sx={{fontWeight: 'bold', width: '18ch'}}
            >
                
            </Typography>
            <Typography>
              {details.posblFcltyCl}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}

      {details.intro ? (
        <Stack>
          <Stack direction="row" >
            <Typography>
              {details.intro}
            </Typography>
          </Stack>
          <Divider sx={{ borderBottomWidth: 3, my: 1 }} />
        </Stack>
      ) : null}
      
    </Stack>
  )
      
}

export default Info;