import React, { useState, useCallback } from 'react';
import axios from 'axios'
import { Container, TextField, Box, CssBaseline, Typography, Button, Link, Grid } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Logo from "../img/logo.png";
import Avatar from '@mui/material/Avatar';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import StickyFooter from '../components/common/Footer';

const theme = createTheme();
const BASE_URL_USER = 'i6c109.p.ssafy.io:8000';

export default function SignUp() {
  //, , , ,     
  const [id, setId] = useState('')
  const [nickname, setNickname] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [passwordConfirm, setPasswordConfirm] = useState('')

  // 
  const [idMessage, setIdMessage] = useState('')
  const [nicknameMessage, setNicknameMessage] = useState('')
  const [emailMessage, setEmailMessage] = useState('')
  const [passwordMessage, setPasswordMessage] = useState('')
  const [passwordConfirmMessage, setPasswordConfirmMessage] = useState('')

  //  
  const [isId, setIsId] = useState(false)
  const [isNickname, setIsNickname] = useState(false)
  const [isEmail, setIsEmail] = useState(false)
  const [isPassword, setIsPassword] = useState(false)
  const [isPasswordConfirm, setIsPasswordConfirm] = useState(false)

  //  
  const [ userNickname, setUserNickname ] = useState();
  const [ userId, setUserId ] = useState();

  
  const handleJoin = async () => {
    if (isId == true && isNickname == true && isEmail == true && isPassword == true && isPasswordConfirm == true) {
      await axios
        .post('http://i6c109.p.ssafy.io:8000/user/', {
          //   
          id: id,
          password: password,
          nickname: nickname,
          email: email,
        }).then((res) => {
          console.log(res)
          alert(" !");
          window.location.href = "http://i6c109.p.ssafy.io/"
        }).catch((error) => {
          console.error(error);
        });
    }
    else
      alert('  ')
  }

  // 
  const onChangeName = useCallback((e) => {
    setUserId(e.target.value)
    setId(e.target.value)
    if (e.target.value.length < 5 || e.target.value.length > 15) {
      setIdMessage('5  15  .')
      setIsId(false)
    } else {
      setIdMessage('   :)')
      setIsId(true)
    }
  }, [])

  //   
  const onCheckId = (e) => {
    axios.get(`http://i6c109.p.ssafy.io:8000/user/valid/${userId}`)
    .then(res=> {
      console.log(res)
      if (userId.length < 5 || userId.length > 15) {
        alert('5  15  .')
        setIsId(false)
      } else {
        if (res.data == 'OK' && (5 <= userId.length && userId.length <= 15)) {
          alert('   .')
          setIsId(true)
        } else {
          alert('  .')
          console.log(userId.length)
          setIsId(false)
        }
      }
      
      
    }).catch(e => {
      alert('  ')
    })
  }

   // 
   const onChangeNickname = useCallback((e) => {
    setUserNickname(e.target.value);
    setNickname(e.target.value)
    if (e.target.value.length < 2 || e.target.value.length > 15) {
      setNicknameMessage('2  15  .')
      setIsNickname(false);
    } else {
      setNicknameMessage('   :)')
      setIsNickname(true);
    }
  }, [])

  //   
  const onCheckNickname = (e) => {
    axios.get(`http://i6c109.p.ssafy.io:8000/user/${userNickname}`)
    .then(res => {
      // console.log(res.data[0])
      if (res.data[0] !== undefined) {
        alert('    .')
        setIsNickname(false);
      } else {
        if (userNickname.length < 2 || userNickname.length > 15) {
          alert('2  15  .')
          setIsNickname(false);
        } else {
          alert(' ')
          setIsNickname(true);
        }
      }
    }).catch(e => {
      alert('  ')
    })
  }

   // 
   const onChangeEmail = useCallback((e) => {
    const emailRegex =
      /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/
    const emailCurrent = e.target.value
    setEmail(emailCurrent)

    if (!emailRegex.test(emailCurrent)) {
      setEmailMessage('  !  ')
      setIsEmail(false)
    } else {
      setEmailMessage('   :)')
      setIsEmail(true)
    }
  }, [])

  // 
  const onChangePassword = useCallback((e) => {
    const passwordRegex = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/
    const passwordCurrent = e.target.value
    setPassword(passwordCurrent)

    if (!passwordRegex.test(passwordCurrent)) {
      setPasswordMessage('++  8  !')
      setIsPassword(false)
    } else {
      setPasswordMessage('  :)')
      setIsPassword(true)
    }
  }, [])

  //  
  const onChangePasswordConfirm = useCallback(
    (e) => {
      const passwordConfirmCurrent = e.target.value
      setPasswordConfirm(passwordConfirmCurrent)

      if (password === passwordConfirmCurrent) {
        setPasswordConfirmMessage('   :)')
        setIsPasswordConfirm(true)
      } else {
        setPasswordConfirmMessage(' .  ')
        setIsPasswordConfirm(false)
      }
    },
    [password]
  )

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="sm" sx={{ mb : 4}}>
        <CssBaseline />
        <Box
          component="img"
          sx={{ height: 100, mt: 3, ml: 5 }}
          alt="logo"
          src={Logo}
        ></Box>
        <Box
          sx={{
            marginTop: 5,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar xs={{ m: 2, bgcolor: '#1b5e20' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5" sx={{ mb: 6, mt: 2 }}>
            Sign up
          </Typography>
          <Box component="form" noValidate xs={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={9}>
                <TextField
                  onChange={onChangeName}
                  autoComplete="given-name"
                  name="id"
                  required
                  fullWidth
                  id="id"
                  label=""
                  autoFocus
                />
                {id.length > 0 && (
                  <span className={`message ${isId ? "success" : "error"}`}>
                    {idMessage}
                  </span>
                )}
              </Grid>
              <Grid item xs={3}>
                <Button
                  onClick={onCheckId}
                  variant="contained"
                  size="large"
                  sx={{ width: 126, height: 55 }}
                  style={{backgroundColor: "#2e7d32"}}
                >
                   
                </Button>
              </Grid>
              <Grid item xs={9}>
                <TextField
                  onChange={onChangePassword}
                  required
                  fullWidth
                  name="password"
                  label=" ( +  +   8 )"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                />
                {password.length > 0 && (
                  <span
                    className={`message ${isPassword ? "success" : "error"}`}
                  >
                    {passwordMessage}
                  </span>
                )}
              </Grid>
              <Grid item xs={9}>
                <TextField
                  onChange={onChangePasswordConfirm}
                  required
                  fullWidth
                  name="password-Confirmation"
                  label=""
                  type="password"
                  id="password-Confirmation"
                  autoComplete="new-password"
                />
                {passwordConfirm.length > 0 && (
                  <span
                    className={`message ${
                      isPasswordConfirm ? "success" : "error"
                    }`}
                  >
                    {passwordConfirmMessage}
                  </span>
                )}
              </Grid>
              <Grid item xs={9}>
                <TextField
                  onChange={onChangeNickname}
                  required
                  fullWidth
                  id="nickname"
                  label=""
                  name="nickname"
                  autoComplete="email"
                />
                {nickname.length > 0 && (
                  <span
                    className={`message ${isNickname ? "success" : "error"}`}
                  >
                    {nicknameMessage}
                  </span>
                )}
              </Grid>
              <Grid item xs={3}>
                <Button
                  onClick={onCheckNickname}
                  variant="contained"
                  size="large"
                  sx={{ width: 126, height: 55 }}
                  style={{backgroundColor: "#2e7d32"}}
                >
                   
                </Button>
              </Grid>
              <Grid item xs={9}>
                <TextField
                  onChange={onChangeEmail}
                  required
                  fullWidth
                  id="email"
                  label=""
                  name="email"
                  autoComplete="email"
                />
                {email.length > 0 && (
                  <span className={`message ${isEmail ? "success" : "error"}`}>
                    {emailMessage}
                  </span>
                )}
              </Grid>

            </Grid>
            {/*       */}
            <Button
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 1, height: 55 }}
              style={{ backgroundColor: "#1b5e20", color: '#fafafa' }}
              onClick={handleJoin}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item >
                <Link href="/login" variant="body2" underline='none' style={{color : '#1b5e20'}}>
                    ??  !
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
      <StickyFooter></StickyFooter>
    </ThemeProvider>
  );
}