import React, { useEffect, useState, useRef, useCallback } from 'react';
import axios from 'axios';
import { Container, CssBaseline, Typography, Grid, TextField, Stack, Box, Button } from '@mui/material';
import { styled } from '@mui/material/styles';
import Divider from '@mui/material/Divider';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import NavBar from '../../components/common/NavBar'
import "../../styles/uploader.css"
import dog from '../../img/dog.png'
import { useParams } from 'react-router-dom';
import StickyFooter from '../../components/common/Footer';

const Input = styled('input')({
  display: 'none',
});

const spanStyle ={ 
  marginTop : "10px"
}

export default function Update() {
  const accessToken = localStorage.getItem("accessToken");
  const HEADER = {
    headers: {
      Authorization: accessToken,
    },
  };
  //   
  const [ textLength, setTextLength ] = useState('0');
  
  const onKeyUp = (e) => {
    const totalText = e.target.value;
    const totalLength = totalText.length;
    setTextLength(totalLength);
  }

  //    &  
  const [ userNickname, setUserNickname ] = useState('');

  const [nickname, setNickname] = useState('')
  const [nicknameMessage, setNicknameMessage] = useState('')
  const [isNickname, setIsNickname] = useState(false)
  
  const onChangeNickname = (e) => {
    setUserNickname(e.target.value);
    setNickname(e.target.value);
    if (nickname.length < 2 || nickname.length > 15) {
      setNicknameMessage('2  15  .')
      setIsNickname(false)
    } else {
      setNicknameMessage('   :)')
      setIsNickname(true)
    }
  }

  const onCheckDuplicate = (e) => {
    axios.get(`http://i6c109.p.ssafy.io:8000/user/${userNickname}`,HEADER)
    .then(res => {
      // console.log(res.data[0])
      if (res.data[0] !== undefined) {
        alert('    .')
      } else {
        alert('   .')
      }
    })
  }
  
  //  
  const [password, setPassword] = useState('')
  const [passwordConfirm, setPasswordConfirm] = useState('')

  const [passwordMessage, setPasswordMessage] = useState('')
  const [passwordConfirmMessage, setPasswordConfirmMessage] = useState('')

  const [isPassword, setIsPassword] = useState(false)
  const [isPasswordConfirm, setIsPasswordConfirm] = useState(false)

  // const userId = userInfo[0].id;
  
  const onChangePassword = (e) => {
    const passwordRegex = /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,25}$/
    const passwordCurrent = e.target.value
    setPassword(passwordCurrent)

    if (!passwordRegex.test(passwordCurrent)) {
      setPasswordMessage('++  8  !')
      setIsPassword(false)
    } else {
      setPasswordMessage('  :)')
      setIsPassword(true)
    }
  }

  // ,   
  const { nick } = useParams();
  const [loading, setLoading] = useState(true);
  const [userInfo, setUserInfo] = React.useState('');
  
  const onChangePasswordConfirm = (e) => {
    const passwordConfirmCurrent = e.target.value
      setPasswordConfirm(passwordConfirmCurrent)

      if (password === passwordConfirmCurrent) {
        setPasswordConfirmMessage('   :)')
        setIsPasswordConfirm(true)
      } else {
        setPasswordConfirmMessage(' .  ')
        setIsPasswordConfirm(false)
      }
  }
  // ,   
 
  const [ userIntro, setUserIntro ] = useState('');
  const [ userId, setUserId ] = useState('');
  const [ userEmail, setUserEmail ] = useState('');
  const [ intro, setIntro ] = useState('');

  // console.log(userInfo)

  useEffect(() => {
    axios.get(`http://i6c109.p.ssafy.io:8000/user/${nick}`)
      .then(res => {
        setUserInfo(res.data);
        setLoading(false);
        setUserIntro(res.data[0].intro);
        setUserId(res.data[0].id)
        setUserEmail(res.data[0].email)
      })
  }, []);
  
  //  
  const onChangeIntro = (e) => {
    setIntro(e.target.value)
  }
  // console.log(userInfo)
  const onChangeInfo = (res) => {
    const currentId = userInfo[0].id;
    axios.put(`http://i6c109.p.ssafy.io:8000/user/${currentId}`, {
      "id" : userInfo[0].id,
      "password" : password,
      "email" : userInfo[0].email,
      "nickname" : nickname,
      "intro" : intro
    })
    .then(res => {
      if (res.status === 200) {
      alert(' .')
      window.location.href = `/profile/${nick}`
    }})
  }
  console.log(nickname, intro)

  //  
  const [ image, setImage ] = useState({
    image_file : "",
    preview_URL : dog,
  });

  const [ loaded, setLoaded ] = useState(false);
  let inputRef;
  
  const saveImage = (e) => {
    e.preventDefault();
    const fileReader = new FileReader();

    if(e.target.files[0]) {
      setLoaded("loading")
      fileReader.readAsDataURL(e.target.files[0])
    } 
    fileReader.onload = () => {
      setImage({
        image_file : e.target.files[0],
        preview_URL : fileReader.result
      })
      setLoaded(true);
    }
  }
  
  const sendImageToServer = async () => {
    if (image.image_file) {
      const formData = new FormData()
      formData.append('file', image.image_file);
      const fileReader = new FileReader();
      fileReader.readAsDataURL(image.image_file);

      fileReader.onload = function () {
        const fileImage = fileReader.result;
        console.log(fileImage)
        axios
          .put(`http://i6c109.p.ssafy.io:8000/user/${userId}`,
            { "photo": fileImage },HEADER)
          .then(res => {
            console.log(res);
            alert(" .")
          });
        setLoaded(false);
   		};
    } else {
      alert(" !")
    }
  }

  return (
    <div>
      {loading ? null : (
        <div>
          <NavBar></NavBar>
          <CssBaseline />
          <Container maxwidth="lg" sx={{ mt: 13 }}>
            <h2>  </h2>
            <Grid container>
              <Grid item xs={3}>
                {/* <AccountCircleIcon sx={{ fontSize: 150 }} /> */}
                {/* <Box sx={{ height : 100, ml : 2 }}> */}
                  {/* <img src={require("../../img/dog.png")} alt='109' width="120px" /> */}
                {/* </Box> */}
                <div className="uploader-wrapper">
                  <input type="file" accept="image/*"
                    onChange={saveImage}
                    ref={refParam => inputRef = refParam}
                    style={{ display: "none" }}
                  />
                </div>
                <div className="img-wrapper">
                  {loaded === false || loaded === true ? (
                    <img src={image.preview_URL} alt="109"/>
                  ) : (
                    <span>  .</span>
                  )}
                </div>
                <div className="upload-button" >
                  <Stack direction="column">
                    <Button 
                      variant="contained" 
                      sx={{ mt: 5, mr : 1, width: "23ch" }} 
                      type="primary" 
                      onClick={() => inputRef.click()}
                      style={{
                        color: "white",
                        backgroundColor: "#43a047"
                      }}
                    >
                       
                    </Button>
                    <Button 
                      variant="contained" 
                      component="span" 
                      sx={{ mt: 5, width: "23ch" }} 
                      onClick={sendImageToServer}
                      style={{
                        color: "white",
                        backgroundColor: "#43a047"
                      }}
                    >
                        
                    </Button>
                  </Stack>
                </div>
              </Grid>
              <Grid item xs={9}>
                <Stack direction="row" sx={{ mt: 2 }}>
                  <Typography></Typography>
                  <Typography sx={{ ml: "13ch", fontWeight: "bold", mb: 2 }}>
                    {userId}
                  </Typography>
                </Stack>
                <Stack direction="row" sx={{ mt: 2 }}>
                  <Typography></Typography>
                  <Typography sx={{ ml: "13ch", fontWeight: "bold", mb: 2 }}>
                    {userEmail}
                  </Typography>
                </Stack>
                <Stack direction="row" sx={{ mt: 2 }}>
                  <Typography sx={{ mr: 2, width: 150 }}></Typography>
                  <TextField
                    onChange={onChangeIntro}
                    onKeyUp={onKeyUp}
                    sx={{ width: "50ch" }}
                    multiline
                    defaultValue={userIntro}
                  />
                  <Typography sx={{ m: 2 }}> {textLength} / 150</Typography>
                </Stack>
                <Stack direction="row" sx={{ mt: 2 }}>
                  <Typography sx={{ mr: 2, width: 150 }}></Typography>
                  <Stack>
                    <TextField
                      onChange={onChangeNickname}
                      sx={{ width: "50ch" }}
                      defaultValue={userNickname}
                    >
                    </TextField>
                    <div style={spanStyle}>
                      {nickname.length > 0 && (
                        <span
                          className={`message ${
                            isNickname ? "success" : "error"
                          }`}
                        >
                          {nicknameMessage}
                        </span>
                      )}
                    </div>
                  </Stack>
                  <Button
                    variant="contained"
                    onClick={onCheckDuplicate}
                    sx={{ ml: 2, width: "16ch", height: '7ch' }}
                    style={{
                      color: "white",
                      backgroundColor: "#43a047"
                    }}
                  >
                     
                  </Button>
                </Stack>
                <Divider sx={{ borderBottomWidth: 2, my: 2 }} />
                <Box>

                  <Stack direction="row" sx={{ mt: 2 }}>
                    <Typography sx={{ mr: 2, width: 150 }}></Typography>
                    <Stack>
                      <TextField
                        onChange={onChangePassword}
                        sx={{ width: "50ch" }}
                        placeholder="    ."
                        type="password"
                      />
                      <div style={spanStyle}>
                        {password.length > 0 && (
                          <span
                            className={`message ${
                              isPassword ? "success" : "error"
                            }`}
                          >
                            {passwordMessage}
                          </span>
                        )}
                      </div>
                    </Stack>
                  </Stack>
                  <Stack direction="row" sx={{ mt: 2 }}>
                    <Typography sx={{ mr: 2, width: 150 }}>
                       
                    </Typography>
                    <Stack>
                      <TextField
                        sx={{ width: "50ch" }}
                        onChange={onChangePasswordConfirm}
                        type="password"
                      />
                      <div style={spanStyle}>
                        {passwordConfirm.length > 0 && (
                          <span
                            className={`message ${
                              isPasswordConfirm ? "success" : "error"
                            }`}
                          >
                            {passwordConfirmMessage}
                          </span>
                        )}
                      </div>
                    </Stack>
                  </Stack>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box sx={{ mx : 60, my : 3 }}>
                  <Button
                    onClick={onChangeInfo}
                    variant="contained"
                    sx={{ width: 200, mt: 3 }}
                    color="success"
                  >
                     
                  </Button>
                </Box>
              </Grid>
            </Grid>
          </Container>
        </div>
      )}
      <StickyFooter></StickyFooter>
    </div>
  );
}