import React, { useRef, useState } from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import { Typography, Stack, Grid } from '@mui/material';



export default function RadioButtonsGroup(props) {
	const [isChecked, setIsChecked] = useState();

	function checkOnlyOneCamp(e) {
		const refName = e.target.value;
		props.func1(refName);
	}
	
  return (
    <FormControl>
			<Stack direction="row" spacing={1}>
				<FormLabel id="radio-button" sx={{ fontWeight : 'bold', fontSize : 18 }} >&lt; &gt;</FormLabel>
				<Typography sx={{ fontSize : 13}}> .</Typography>
			</Stack>
		  <RadioGroup
			  onChange={checkOnlyOneCamp}
				row
				aria-labelledby="radio-button"
				name="radio-button-group"
			>
				{/*     !..   */}
				<Grid md={12}>
					<FormControlLabel value=""  control={<Radio />} label="" />
					<FormControlLabel value="" control={<Radio />} label="" />
					<FormControlLabel value="" control={<Radio />} label="" />
					<FormControlLabel value="-" control={<Radio />} label="" />
				</Grid>
					<FormLabel id="radio-button" sx={{ fontWeight : 'bold', fontSize : 18, mt : 1 }}>&lt; &gt;</FormLabel>
				<Grid md={12}>
					<FormControlLabel value=" " control={<Radio />} label=" " />
					<FormControlLabel value="-" control={<Radio />} label="" />
				</Grid>
			</RadioGroup>
			{/* <RadioGroup
				onChange={checkOnlyOneCommunity}
				row
				aria-labelledby="radio-button"
				name="radio-button-group"
			>
				<FormControlLabel value="equipment-review" control={<Radio />} label=" " />
				<FormControlLabel value="free" control={<Radio />} label="" />
			</RadioGroup> */}
    </FormControl>
  );
}